:root {
  --color-white: #ffffff;
  --color-primary: #ffffff;
  --color-primary-text: #494c6b;
  --color-secondary-text: #9394a5;
  --color-light-text: #d4d4d4;
  --color-active: #3a7cfd;
  --color-circle: #979797;
  --color-background: #fafafa;
}

.dark {
  --color-primary: #25273c;
  --color-primary-text: #e4e5f1;
  --color-secondary-text: #cacde8;
  --color-deleted-text: #4d5067;
  --color-circle: #777a92;
  --color-background: #161722;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body {
  font-family: "Josefin Sans", sans-serif;
  background-color: var(--color-background);
}

ul {
  list-style: none;
}

h1.main-title {
  color: var(--color-white);
  text-transform: uppercase;
  letter-spacing: 6px;
}

input:focus {
  outline-color: var(--color-active);
}

input:placeholder-shown {
  color: var(--color-secondary-text);
}

.toggle-modus {
  cursor: pointer;
  transition: 0.3s;
}
.toggle-modus:hover {
  filter: brightness(1) sepia(1) saturate(10000%);
}

.circle {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  border: 1px solid var(--color-circle);
}

.todo__list--item {
  display: flex;
  justify-content: space-between;
  gap: 0.75rem;
  padding: 1.25rem;
  color: var(--color-primary-text);
}
.todo__list--item:not(l:last-of-type) {
  border-bottom: 1px solid var(--color-light-text);
}
.todo__list--item p {
  margin-right: auto;
}
.todo__list--item .checked-icon {
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.2s, visibility 0.2s;
}
.todo__list--item .circle {
  cursor: pointer;
}
.todo__list--item .circle[data-checkable=true] {
  background: linear-gradient(135deg, #57ddff, #c058f3) no-repeat center/cover;
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
}
.todo__list--item .circle[data-checkable=true] + p {
  text-decoration: line-through;
  color: var(--color-light-text);
}

.todo__list--item.checked .checked-icon {
  opacity: 1;
  visibility: visible;
  pointer-events: none;
}

.delete-icon {
  width: 20px;
  height: 20px;
  cursor: pointer;
}
.delete-icon svg path {
  transition: fill 0.4s;
}
.delete-icon:hover svg path {
  fill: crimson;
}

header .background {
  width: 100vw;
  height: 250px;
}
header img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.container {
  position: absolute;
  width: min(90%, 540px);
  left: 50%;
  transform: translateX(-50%);
  top: 3rem;
}
.container__heading {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 2.5rem;
}

#form__todo {
  background-color: var(--color-primary);
  display: block;
  padding: 1.15rem 1.125rem;
  border-radius: 5px;
  margin-bottom: 2rem;
  display: flex;
  align-items: center;
  gap: 0.75rem;
}
#form__todo input {
  flex: 1;
  border: 0;
  padding: 0.5rem;
  font-family: inherit;
  color: var(--color-secondary-text);
  font-size: 0.85rem;
  caret-color: var(--color-active);
}

.todo__list {
  border-radius: 5px;
  overflow: hidden;
  background-color: var(--color-primary);
  box-shadow: -5px 8px 30px 8px rgba(0, 0, 0, 0.08);
}

.todo__list--options,
.todo__list--sort {
  color: var(--color-secondary-text);
  display: flex;
  align-items: center;
  padding: 1.25rem;
}
.todo__list--options p,
.todo__list--sort p {
  cursor: pointer;
  transition: color 0.3s;
}
.todo__list--options p:hover,
.todo__list--sort p:hover {
  color: var(--color-active);
}

.todo__list--options {
  justify-content: space-between;
}

.todo__list--sort {
  justify-content: center;
  margin-top: 1rem;
  background-color: var(--color-primary);
  border-radius: 5px;
  gap: 1.25rem;
  box-shadow: -5px 8px 30px 8px rgba(0, 0, 0, 0.08);
}
.todo__list--sort.desktop {
  display: none;
}
.todo__list--sort p.active {
  color: var(--color-active);
}
@media screen and (min-width: 768px) {
  .todo__list--sort.mobile {
    display: none;
  }
  .todo__list--sort.desktop {
    display: block;
    display: flex;
    margin-top: 0;
    gap: 1.25rem;
    box-shadow: none;
  }
}

.todo__drag-drop--text {
  text-align: center;
  color: var(--color-secondary-text);
  margin-top: 3rem;
}